// app/page.tsx
import { Suspense } from 'react'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { getTrendingAnimeGogo, getPopularAnimeGogo } from '@/lib/api'
import { HeroSkeleton, StaticSkeletonGrid, FlexSkeletonGrid, FunSkeletonCardCompact } from '@/components/skeleton-loader'
import { HeroCarousel } from '@/components/hero-carousel'

// ========================
// üé¨ Helper: Mapping Gogoanime -> HeroCarousel
// ========================
function mapGogoToCarousel(animes: any[]) {
  return animes.map(a => ({
    mal_id: a.animeId,              // HeroCarousel expects mal_id
    title: a.title,
    images: { jpg: { image_url: a.image } },  // images.jpg.image_url
  }))
}

// ========================
// üì∫ Sections
// ========================
async function HeroSection() {
  const trending = await getTrendingAnimeGogo()
  const animes = mapGogoToCarousel(trending.slice(0, 5))
  return <HeroCarousel animes={animes} />
}

async function TopSection() {
  const trending = await getTrendingAnimeGogo()
  const animes = trending.slice(0, 5) // ambil 5 teratas
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-xl sm:text-2xl font-bold text-foreground mb-3 sm:mb-4">
        Top Anime
      </h2>
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-5 gap-3 sm:gap-4 md:gap-6">
        {animes.map((anime, i) => (
          <AnimeCard key={`${anime.animeId}-${i}`} anime={{
            mal_id: anime.animeId,
            title: anime.title,
            images: { jpg: { image_url: anime.image } }
          }} />
        ))}
      </div>
    </div>
  )
}

async function PopularSection() {
  const popular = await getPopularAnimeGogo()
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Popular Anime</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {popular.map((anime, i) => (
          <AnimeCard key={`${anime.animeId}-${i}`} anime={{
            mal_id: anime.animeId,
            title: anime.title,
            images: { jpg: { image_url: anime.image } }
          }} />
        ))}
      </div>
    </div>
  )
}

async function TrendingSection() {
  const trending = await getTrendingAnimeGogo()
  const animes = trending.slice(5, 25)
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Trending This Week</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {animes.map((anime, i) => (
          <AnimeCard key={`${anime.animeId}-${i}`} anime={{
            mal_id: anime.animeId,
            title: anime.title,
            images: { jpg: { image_url: anime.image } }
          }} />
        ))}
      </div>
    </div>
  )
}

// ========================
// üè† Home Page
// ========================
export default function Home() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      {/* Hero Carousel */}
      <Suspense fallback={<HeroSkeleton />}>
        <HeroSection />
      </Suspense>

      {/* Top Anime */}
      <Suspense fallback={<StaticSkeletonGrid count={5} />}>
        <TopSection />
      </Suspense>

      {/* Popular Anime */}
      <Suspense fallback={<FlexSkeletonGrid count={25} />}>
        <PopularSection />
      </Suspense>

      {/* Trending */}
      <Suspense fallback={<FlexSkeletonGrid count={20} />}>
        <TrendingSection />
      </Suspense>
    </main>
  )
}
